import React, { Component } from 'react';
import {
  NavLink
} from 'react-router-dom';




class Header2 extends Component {
  constructor(props) {
      super(props);
      this.toggleNavbar = this.toggleNavbar.bind(this);
      this.state = {
      collapsed: true,
      };
      }
      toggleNavbar() {
      this.setState({
      collapsed: !this.state.collapsed,
      });
      }
      render() {
            const collapsed = this.state.collapsed;
            const classOne = collapsed ? 'collapse navbar-collapse' : 'collapse navbar-collapse show';
            const classTwo = collapsed ? 'navbar-toggler navbar-toggler-right collapsed' : 'navbar-toggler navbar-toggler-right';
            return (
              <nav className="navbar navbar-expand-lg navbar-dark  transparent-nav ">
                <div className="container ">
                  <NavLink exact activeClassName=''to="/" className="title">Cody /<span className="text-secondary">/ Swick</span></NavLink>
                  <button onClick={this.toggleNavbar} className={`${classTwo}`} type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon" />
                  </button>


                  <div className={`${classOne} `} id="navbarResponsive">
                    <ul id="right" className="ml-auto navbar-nav nav-effect">
                      <li>
                        <NavLink exact activeClassName='current'to="/" className="">Home</NavLink>
                      </li>
                      <li>
                        <NavLink exact activeClassName='current'to="/aboutPage" className="">About Me</NavLink>
                      </li>
                      <li>
                        <NavLink exact activeClassName='current'to="/skillsPage" className="">My Skills</NavLink>
                      </li>
                      <li>
                        <NavLink activeClassName='current'to="/myworkPage" className="">My Work</NavLink>
                      </li>
                      <li>
                        <NavLink activeClassName='current'to="/contactPage" className="">Email Me</NavLink>
                      </li>
                    </ul>
                  </div>
                </div>
              </nav>
      );
      }
      }

export default Header2;
